import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersistence from 'vuex-persist'

const vuexLocal = new VuexPersistence({
  storage: window.localStorage
})

Vue.use(Vuex)

export default new Vuex.Store({
    plugins: [vuexLocal.plugin],
    state: {
        products: [
            {
              name: "Pizza Calabresa",
              price: 9.99,
              description: "Pizza brotinho sabor calabresa",
              picture: "images/pizza.jpg"
            },
            {
              name: "Pizza Presunto",
              price: 10.00,
              description: "Pizza brotinho saborosa...",
              picture: "images/pizza.jpg"
            },
            {
              name: "Pizza Frango",
              price: 10.00,
              description: "Pizza brotinho saborosa...",
              picture: "images/pizza.jpg"
            }
        ],
        cart: [],
        address: [],
        ship: 5
    },
    getters: {
        getProducts(state){
            return state.products
        },
        getCart(state){
            return state.cart
        },
        getCartTotal(state){
          return state.cart.map(c => c.quantity * c.value)
            .reduce((total, atual) => total + atual, 0)
        },
        getAddress(state){
            return state.address
        },
        getShip(state){
            return state.ship
        }
    },
    mutations: {
        addProductToCart(state, payload){
            state.cart.push(payload)
        },
        delProductToCart(state, payload){
            const product = state.cart.filter(item => item.id !== payload)
            state.cart = product
        },
        clearCart(state){
            state.cart = []
        },
        storeAddress(state, payload){
            if (state.address.length < 3) {
                const index = state.address.findIndex((e) => e.id === payload.id);
                if (index === -1){
                    state.address.push(payload)
                } else {
                    state.address[index] = payload
                }
            }
        },
        delAddress(state, payload){
            const address = state.address.filter(item => item.id !== payload)
            state.address = address
        }
    }

})