<template>
    <div class="cart">
        <span class="cart-title">Seu pedido</span>
        <hr />
        <b-row v-for="item in cart" v-bind:key="item.id" class="product">
            <b-col>
                <b-row>
                    <b-col md="8">
                        <span class="semibold">{{ item.name }}</span>
                    </b-col>
                    <b-col md="4" class="text-right">
                        <span class="bold">{{ item.value | money }}</span>
                    </b-col>
                </b-row>
                <b-row>
                    <b-col class="product-description">
                        {{ item.description }}
                    </b-col>
                </b-row>
                <b-row>
                    <b-col md="6">
                        <a href="#" class="semibold" @click.prevent="delProduct(item)">Remover</a>
                    </b-col>
                    <b-col md="6">

                    </b-col>
                </b-row>
                <hr/>
            </b-col>
        </b-row>

        <div class="total">
        <b-row>
            <b-col>
                <b-row>
                    <b-col md="8">
                        Subtotal
                    </b-col>
                    <b-col md="4" class="text-right">
                        {{ totalCart | money }}
                    </b-col>
                </b-row>
            </b-col>
        </b-row>
        <b-row>
            <b-col>
                <b-row>
                    <b-col md="8">
                        Entrega
                    </b-col>
                    <b-col md="4" class="text-right">
                        {{ ship | money }}
                    </b-col>
                </b-row>
            </b-col>
        </b-row>
        <b-row>
            <b-col class="text-red">

                <b-row>
                    <b-col md="8">
                        Total
                    </b-col>
                    <b-col md="4" class="text-right">
                        {{ (totalCart + ship) | money }}
                    </b-col>
                </b-row>
            </b-col>
        </b-row>
        </div>

        <hr/>
        <b-button class="btn btn-block btn-send-order bg-red"
        v-b-modal.address-modal
        v-bind:class="{ 'disabled': !notEmpty }" :disabled="!notEmpty">
            Enviar meu pedido pelo whatsapp
        </b-button>

    </div>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex'
export default {
    computed: {
        ...mapGetters({
            cart: 'getCart',
            totalCart: 'getCartTotal',
            ship: 'getShip'
        }),
        notEmpty(){
            if(this.cart.length > 0){
                return true
            }
            return false
        }
    },
    methods: {
        ...mapMutations(['delProductToCart']),
        delProduct(product){
            this.delProductToCart(product.id)
        },
        sendOrder(){
            let message = "Olá, gostaria realizar um pedido:%0A"
            this.cart.forEach(item => {
                message += item.quantity + "x " + item.name + " (" + "R$ " + this.$options.filters.money(item.value) + ")%0A"
            });
            message += "Total: " + this.$options.filters.money(this.totalCart)
            window.location.href = "https://api.whatsapp.com/send?phone=5574991414581&text=" + message
        },
    },
    filters:{
        money(value) {
            let val = (value/1).toFixed(2).replace('.', ',')
            return "R$ " + val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")
        }
    },
}
</script>

<style>
    .cart{
        background: #fff;
        /*margin-top: -20px;*/
        min-height: 84vh;
        height: 100%;
        padding: 23px 30px;
        border-top-left-radius: 50px;
        -webkit-box-shadow: 0px 3px 6px 0px rgba(0,0,0,0.16);
        -moz-box-shadow: 0px 3px 6px 0px rgba(0,0,0,0.16);
        box-shadow: 0px 3px 6px 0px rgba(0,0,0,0.16);
    }
    .cart-title{
        font-size: 18px;
        font-weight: 600;
    }
    hr{
        margin: 1.5rem 0 !important;
    }
    .product{
        padding: 10px 0;
    }
    .product-description{
        padding: 10px;
    }
    a{
        color: #9D9D9D !important;
    }
    .total{
        font-size: 18px;
        font-weight: 600;
    }
    .btn-send-order{
        border-radius: 50px !important;
        border: none !important;
        height: 50px;
        color: white !important;
        font-weight: 600 !important;
        -webkit-box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.16);
        -moz-box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.16);
        box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.16);
    }
    @media (max-width: 853px) {
        .btn-send-order{
            font-size: 14px !important;
        }
    }
</style>