<template>
    <div>
        <b-modal
            id="address-modal"
            title="Endereço"
            size="lg"
            cancel-disabled
            ok-disabled
            no-close-on-backdrop
            @ok="sendOrder"
            @cancel="addAddress">
            <b-row class="p-15">
                <b-col md="3" v-for="address in address" :key="address.id">
                    <b-card :title="address.name | limitChar(12)">
                        <a class="delete-address" href="#" @click.prevent="delAddress(address.id)">trash</a>
                        <p>
                            {{ address.street | limitChar }}
                        </p>
                        <a href="#" @click.prevent="useAddress(address)">Selecionar</a>
                    </b-card>
                </b-col>
            </b-row>

            <hr/>

            <b-row class="p-15">
                <b-col md="12">
                    <b-alert show variant="danger" v-if="errors.length">
                        <b>Por favor, corrija o(s) seguinte(s) erro(s):</b>
                        <ul>
                            <li v-for="error in errors" :key="error">{{ error }}</li>
                        </ul>
                    </b-alert>
                    <b-form>
                        <b-row>
                            <b-col>
                                <b-form-input
                                        type="text"
                                        v-model="formAddress.name"
                                        required
                                        placeholder="Nome"
                                >
                                </b-form-input>
                            </b-col>
                        </b-row>
                        <b-row>
                            <b-col>
                                <b-form-input
                                        type="text"
                                        v-model="formAddress.phone"
                                        required
                                        placeholder="Telefone"
                                >
                                </b-form-input>
                            </b-col>
                        </b-row>
                        <b-row>
                            <b-col>
                                <b-form-input
                                        type="text"
                                        v-model="formAddress.email"
                                        required
                                        placeholder="Email"
                                >
                                </b-form-input>
                            </b-col>
                        </b-row>
                        <b-row>
                            <b-col>
                                <b-form-input
                                    type="text"
                                    v-model="formAddress.street"
                                    required
                                    placeholder="Endereço"
                                >
                                </b-form-input>
                            </b-col>
                        </b-row>
                        <b-row>
                            <b-col>
                                <b-form-input
                                    type="text"
                                    v-model="formAddress.neighborhood"
                                    required
                                    placeholder="Bairro"
                                >
                                </b-form-input>
                            </b-col>
                        </b-row>
                        <b-row>
                            <b-col>
                                <b-form-input
                                    type="text"
                                    v-model="formAddress.reference"
                                    placeholder="Ponto de referência"
                                >
                                </b-form-input>
                            </b-col>
                        </b-row>
                    </b-form>
                </b-col>
            </b-row>

            <template v-slot:modal-footer="{ 'ok': sendOrder, 'cancel': addAddress }">
                <b-row style="width: 100%; padding-bottom: 10px">
                    <b-col md="6">
                        <b-button class="bg-red" @click="addAddress">
                            Adicionar endereço
                        </b-button>
                    </b-col>
                    <b-col md="6" class="text-right">
                        <b-button class="bg-red" @click="sendOrder">
                            Finalizar pedido
                        </b-button>
                    </b-col>
                </b-row>
            </template>
        </b-modal>
    </div>
</template>
<script>
    import { mapGetters, mapMutations } from 'vuex'
    import { v4 as uuidv4 } from 'uuid';
    export default {
        name: 'Address',
        data(){
            return{
                formAddress: {},
                errors: []
            }
        },
        computed: {
            ...mapGetters({
                cart: 'getCart',
                address: 'getAddress',
                totalCart: 'getCartTotal',
                ship: 'getShip'
            })
        },
        methods:{
            ...mapMutations(['storeAddress', 'delAddress', 'clearCart']),
            useAddress(address){
                this.formAddress = address
            },
            addAddress(e){
                e.preventDefault()
                if(this.checkForm()){
                    this.formAddress.id = this.formAddress.id ?? uuidv4()
                    this.storeAddress(this.formAddress)
                    this.formAddress = []
                }
            },
            checkForm() {
                this.errors = [];

                if (this.formAddress.name &&
                    this.formAddress.street &&
                    this.formAddress.neighborhood
                ) {
                    return true;
                }

                if (!this.formAddress.name) {
                    this.errors.push('O nome é obrigatório.');
                }
                if (!this.formAddress.street) {
                    this.errors.push('A rua é obrigatória.');
                }
                if (!this.formAddress.neighborhood) {
                    this.errors.push('O bairro é obrigatório.');
                }

                return false
            },
            sendOrder(){
                let message = "Olá, gostaria realizar um pedido:%0A %0A"
                message += "*Nome:* " + this.formAddress.name + "%0A"
                message += "*Telefone:* " + this.formAddress.phone + "%0A"
                message += "*Endereço:* " + this.formAddress.street + ", " +
                    this.formAddress.neighborhood + "%0A"
                message += "*Referência:* " + this.formAddress.reference + "%0A %0A"
                message += "*Pedido* %0A"
                message += "------------------------------------------------%0A %0A"
                this.cart.forEach(item => {
                    message += item.quantity + "x " + item.name + " (" + "R$ " + this.$options.filters.money(item.value) + ")%0A"
                });
                message += "%0A------------------------------------------------%0A"
                message += "*Subtotal:* " + this.$options.filters.money(this.totalCart) + "%0A"
                message += "*Entrega:* " + this.$options.filters.money(this.ship) + "%0A"
                message += "*Total:* " + this.$options.filters.money(this.totalCart + this.ship)
                this.clearCart()
                window.location.href = "https://api.whatsapp.com/send?phone=5574991414581&text=" + message
            }
        },
        filters:{
            limitChar(text, limit = 20){
                if(text)
                    return text.substring(0, limit) + '...'
            },
            money(value) {
                let val = (value/1).toFixed(2).replace('.', ',')
                return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")
            }
        }
    }
</script>
<style>
    .p-15{
        padding: 0 15px !important;
    }
    .modal{
        color: #757575;
    }
    .card-title{
        font-size: 14px;
        font-weight: 600;
    }
    .card-body{
        font-size: 12px;
    }
    .modal-content{
        border: none !important;
        border-radius: 30px !important;
    }
    .modal-header{
        background: #E40E0C;
        color: white;

    }
    button.close{
        color: white;
        opacity: unset;
    }
    button.close:hover{
        color: white;
        opacity: unset;
    }
    .btn.bg-red{
        border-radius: 50px !important;
        color: white !important;
        font-weight: 600 !important;
        -webkit-box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.16);
        -moz-box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.16);
        box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.16);
    }
    .delete-address{
        position: absolute;
        top: 0px;
        right: 0px;
    }
    .modal-footer{
        border: none !important;
    }
</style>