<template>
    <b-col md="6" sm="12">
        <b-card :title="product.name">
            <b-card-text>
            {{ product.description }}
            </b-card-text>
            <b-row class="card-content">
                <b-col lg="4" md="12">
                    <span class="product-price">
                        {{ product.price | money }}
                    </span>
                </b-col>
                <b-col lg="5" md="12">
                    <b-button-group>
                        
                        <b-button class="bg-red" @click.prevent="decrease()">
                            <font-awesome-icon icon="minus" />
                        </b-button>

                        <b-form-input v-model="qnt" type="number" min="0" value="0" size="3" class="text-center pull-left"></b-form-input>
                        
                        <b-button class="bg-red" @click.prevent="acrease()">
                            <font-awesome-icon icon="plus" />
                        </b-button>
                    </b-button-group>
                </b-col>
                <b-col lg="3" md="12">
                    <b-button block class="bg-red add-cart" @click="addToCart(product)">
                        <img src="../assets/basket.png" width="16"/>
                    </b-button>
                </b-col>
            </b-row>
        </b-card>
    </b-col>
</template>
<script>
import { mapMutations } from 'vuex'
import { v4 as uuidv4 } from 'uuid';

    export default {
        name: 'product-cards',
        props: ['product'],
        data() {
            return {
                qnt: 0
            }
        },
        filters:{
            money(value) {
                let val = (value/1).toFixed(2).replace('.', ',')
                return "R$ " + val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")
            }
        },
        methods:{
            ...mapMutations(['addProductToCart']),
            addToCart(product){
                const item = {
                    id: uuidv4(),
                    name: product.name,
                    quantity: this.qnt,
                    value: product.price
                }
                if(this.qnt > 0){
                    this.addProductToCart(item)
                    this.qnt = 0              
                }
            },
            acrease(){
                this.qnt = this.qnt+1
            },
            decrease(){
                if(this.qnt > 0){
                    this.qnt = this.qnt-1
                }
            }
        }
    }
</script>
<style scoped>
    .card{
        border: none;
        border-radius: 30px;
        -webkit-box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.16);
        -moz-box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.16);
        box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.16);
        margin-bottom: 20px;
        padding: 5px;
    }
    .card-title{
        font-weight: 600;
        font-size: 18px;
    }
    .product-price{
        position: absolute;
        bottom: 0px;
        font-weight: 900;
        font-size: 18px;
    }
    input{
        border: none;
        margin-top: -7px !important;
    }
    .bg-red{
        border: none;
    }
    .btn-group{
        border-radius: 50px;
        padding: 8px;
        height: 40px;
        float: right;
        -webkit-box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.16);
        -moz-box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.16);
        box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.16);
    }
    .btn-group > .btn{
        border-radius: 100% !important;
        width: 20px;
    }
    .btn-group > .btn > svg {
        width: 8px;
        position: absolute;
        top: 4px;
        left: 9px;
    }
    .add-cart{
        border-radius: 50px;
        height: 40px;
        -webkit-box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.16);
        -moz-box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.16);
        box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.16);
    }

    /* Chrome, Safari, Edge, Opera */
    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
    }

    /* Firefox */
    input[type=number] {
    -moz-appearance: textfield;
    }
    @media (max-width: 853px) {
        .card-content{
            height: 120px;
        }
        .product-price{
            position: unset;
        }
        .btn-group{
            margin: 10px 0;
        }
    }

    @media (max-width: 411px) {
        .card-content{
            height: 120px;
        }
        .product-price{
            position: unset;
        }
        .btn-group{
            margin: 10px 0;
        }
    }
</style>